// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import './common/icon/iconfont.css'
import moment from 'moment';
import global_ from './components/tool/Global'
import { baseStore } from './config/baseStore.js';

Vue.config.productionTip = false
Vue.use(ElementUI);
Object.defineProperty(Vue.prototype, '$moment', { value: moment }); //可以全局使用
Object.defineProperty(Vue.prototype, '$global', { value: global_ }); //可以全局使用
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App,ElementUI },
  store: {
    baseStore:baseStore,
  },
  mounted: function (){
    var currentSelectMenu = this.$store.baseStore.getters.getCurrentSelectMenu;
    console.log(currentSelectMenu.firstDegreeMenu )
    if(currentSelectMenu != undefined && currentSelectMenu.firstDegreeMenu != undefined
         && currentSelectMenu.firstDegreeMenu != ''){
        this.$router.push({ path: currentSelectMenu.firstDegreeMenu });
    }else{
        this.$router.push({ path: "login" });
    }
  }
}).$mount('#app');
//router.push('index');





