<template>
  <div class="menu-nav" ref="menuNav">
    <div class="flex-left" ref="menu_Nav">
      <button @click="toggleSet" :class="{'el-icon-arrow-right flew-left-icon':isA,'el-icon-arrow-left flew-left-icon':!isA}" style="color:#30b4d8"></button>
      <div>
        <div :class="{'itemAll': true, 'logout-sidenav': item.modelName === 'dengchu'}" v-for="item in items">
          <ul class="itemlist">
            <li :class="{item,[item.addSelf]:true,'selected--top-menu': item.modelName === selectedMenu}" v-if="(!menuDictionary[item.modelName] || itemShowControl.indexOf(menuDictionary[item.modelName]) > -1) && item.modelName !== 'dengchu'" @click="selectMenuItem(item.modelName)">
              <router-link class="item" :to="item.topage"  v-on:click.native="menuChange(item)" >
                <el-badge v-if="isA == true" :value="itemsNotification[item.modelName]" class="item side-menu--frame" :max="99" :hidden="Object.keys(itemsNotification).indexOf(item.modelName) < 0 || itemsNotification[item.modelName] === 0">
                   <el-tooltip  :content="item.text" placement="right">
                   
                    <i class="iconfont" :class="{[item.userName]:true}"></i>
                    <i class="flew-left-menusummary">{{item.text}}</i>
                   
                   </el-tooltip>
                </el-badge>
                <el-badge v-if="isA == false" :value="itemsNotification[item.modelName]" class="item side-menu--frame" :max="99" :hidden="Object.keys(itemsNotification).indexOf(item.modelName) < 0 || itemsNotification[item.modelName] === 0">
                    <i class="iconfont" :class="{[item.userName]:true}"></i>
                    <i class="flew-left-menusummary">{{item.text}}</i>
                </el-badge>
              </router-link>
            </li>
            <li v-if="item.modelName === 'dengchu'" :class="{item,[item.addSelf]:true}" >
              <a class="item" href="javascript:void(0);" @click="logoutAction()">
                  <i  :class="{[item.userName]:true}"></i>
                  <i class="flew-left-menusummary">{{item.text}}</i>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<!-- <script type="text/ecmascript-6"> -->
<script>
import {
   logout,
}from '@/api/getData'
export default {
    data() {
        return {
            isA: false,
            showStatus: false,
            showTriangle: true,
            userAvatar: '', // 头像
            userStatus: '', 
            statusList: [], // 状态列表
            menuDictionary: {
                'huawuzuoxi': '1',
            // 'zaixianzixun': '2',
                'gongdanzhongxin': '3',
                'kehuziliao': '4',
                'zhishiku': '5',
                'xitongjiankong': '6',
                'tongjibaobiao': '7',
                'gerenshezhi': '8',
                'liuyan': '9',
                'xitongshezhi': '10',
            },
            //状态显示  
            iscircle: true,
            isbusy: false,
            isleave: false,
            menuSetonline: false,
            menuSetbusy: true,
            menuSetleave: true,
            itemShowControl: [], // 是否显示菜单项
            itemsNotification: {
                'hujiaozhongxin': 0, 
                'zaixianzixun': 0,
            }, // 需要消息提示小红点的菜单
            items: [
            ], //点击后，触发请求/extensionGroup  然后去router中看真正的路径
            selectedMenu: '', // 选中的菜单项
        }   
    },
    created() {
         let userInfo = this.$store.baseStore.getters.getUserInfo;
         let menu = userInfo.menu.firstmenuset;
         if(menu.indexOf("账号详情") != -1){
             this.$data.items.push({ topage: '/extAccount', userName: 'iconfont ucc-shouye2 flew-left-menuicon', text: '账号详情', modelName: 'extAccount', });
         }
         if(menu.indexOf("企业账户") != -1){
             this.$data.items.push({ topage: '/enterpriseAccount', userName: 'iconfont ucc-shouye2 flew-left-menuicon', text: '企业账户', modelName: 'enterpriseAccount', });
         }
         if(menu.indexOf("分机管理") != -1){
             this.$data.items.push({ topage: '/extension', userName: 'iconfont ucc-shouye2 flew-left-menuicon', text: '分机管理', modelName: 'index', });
         }
         if(menu.indexOf("租户管理") != -1){
             this.$data.items.push({ topage: '/enterpriseManage', userName: 'iconfont ucc-shouye2 flew-left-menuicon', text: '租户管理', modelName: 'enterpriseManage', });
         }
         if(menu.indexOf("运营报表") != -1){
             this.$data.items.push({ topage: '/oprateChart', userName: 'iconfont ucc-shouye2 flew-left-menuicon', text: '运营报表', modelName: 'oprateChart', });
         }
         if(menu.indexOf("话务管理") != -1){
             this.$data.items.push({ topage: '/call', userName: 'iconfont ucc-icon7 flew-left-menuicon', text: '话务管理', modelName: 'call'});
         }
         if(menu.indexOf("通话记录") != -1){
             this.$data.items.push({ topage: '/phoneRecord', userName: 'iconfont ucc-shouye2 flew-left-menuicon', text: '通话记录', modelName: 'phoneRecord', });
         }
         if(menu.indexOf("设置") != -1){
             this.$data.items.push({ topage: '/setting', userName: 'iconfont ucc-icon7 flew-left-menuicon', text: '设置', modelName: 'setting'});
         }
         this.$data.items.push({ addSelf: 'menu-tuichu', topage: '/', userName: 'iconfont ucc-icon5 flew-left-menuicon', text: '登出', modelName: 'dengchu', });
         

    },
    methods: {
        commonEvent(args){
            this.$emit('commonEvent',args);
        },
        selectMenuItem(menuItem) {
            this.selectedMenu = menuItem;
        },

        menuChange: function(item){
            var args = {};
            args.type = 'menuChange';
            args.modelName = item.modelName;
            args.text = item.text;
            this.commonEvent(args);
        },

        toggleSet: function() {
            this.isA = !this.isA;
            var args = {};
            args.type = 'menuCollapse';
            args.isA = this.isA;
            //this.commonEvent(args);
            if (this.isA) {
                this.$refs.menuNav.style.width = '70px';
                this.$refs.menu_Nav.style.width = '70px';
                this.$refs.menu_Nav.className += ' menuFontChange';
                
            } else {
                this.$refs.menuNav.style.width = '130px';
                this.$refs.menu_Nav.style.width = '130px';
                this.$refs.menu_Nav.className = this.$refs.menu_Nav.className.replace('menuFontChange', '');
               
            }
        },
        //退出登录  
        async logoutAction() {
            try {  
                const res = await logout();
                if (res.resultCode == '0') {
                    this.$store.baseStore.commit('dropUserInfo');
                    this.$router.push({ path: "/login" });
                    
                    var menu = new Object();
                    menu['secondDegreMenu'] = '';
                    menu['firstDegreeMenu'] = '';
                    this.$store.baseStore.commit('setCurrentSelectMenu',menu);
                }else {
                    throw new Error(res.result)
                }
            } catch (err) {
                console.log('获取数据失败', err);
            }
        },

    }
}    
</script>

