import Vue from 'vue'
import Router from 'vue-router'
import index from '../components/index/index.vue'
import login from '../components/index/login.vue'

const extAccount = resolve => require(['../components/extAccount/extAccountmenu.vue'], resolve);
const phoneRecord = resolve => require(['../components/phoneRecord/phoneRecordmenu.vue'], resolve);
const enterpriseManage = resolve => require(['../components/enterpariseManage/enterpriseManagemenu.vue'], resolve);
const enterpriseAccount = resolve => require(['../components/enterpriseAccount/enterpriseAccountmenu.vue'], resolve);
const oprateChart = resolve => require(['../components/oprateChart/oprateChartmenu.vue'], resolve);
const extension = resolve => require(['../components/extension/extendmenu.vue'], resolve);
const call = resolve => require(['../components/call/callmenu.vue'], resolve);
const setting = resolve => require(['../components/setting/settingmenu.vue'], resolve);

Vue.use(Router)
export default new Router({
  routes: [
  
   
    {
        path: '/index',
        component: index,
        children: [
            {
                path: '/extAccount',
                component: extAccount
            },
            {
                path: '/enterpriseManage',
                component: enterpriseManage
            },
            {
                path: '/phoneRecord',
                component: phoneRecord
            },
            {
                path: '/oprateChart',
                component: oprateChart
            },
            
            {
                path: '/enterpriseAccount',
                component: enterpriseAccount
            },
            {
                path: '/extension',
                component: extension
            },
            {
                path: '/call',
                component: call
            },
            {
                path: '/setting',
                component: setting
            },

        ]
    },
    {
        path: '/login',
        component: login,
        
    }, 
  ]
})






