import { baseUrl } from './env'
import router from '../router'

export default async(url = '', data = {}, type = 'GET', method = 'fetch') => {
  var currentWindow = window.name;
  var historyWindow = window.localStorage.getItem("windowname");
  if(url != '/ucc/logout' && url !='/ucc/login/checkLogin'){
    if(currentWindow != undefined && historyWindow != undefined && currentWindow != '' && historyWindow != '' && currentWindow != historyWindow){
        window.name = historyWindow;
        window.location.reload();
      }
  }
  
//window.location.reload();
  type = type.toUpperCase();
  if(url.startsWith("/")){
    url = baseUrl + url;
  }
  var dataStr = ''; //数据拼接字符串
  dataStr = parseObject(data,dataStr);
  if (dataStr !== '') {
    dataStr = dataStr.substr(0, dataStr.lastIndexOf('&'));
  }
  if (type == 'GET' ) {
    url = url + '?' + dataStr;
  }

  if (window.fetch && method == 'fetch') {
    let requestConfig = {
      credentials: 'include',
      method: type,
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
      },
      mode: "cors",
      cache: "no-cache"
    }

    if (type == 'POST') {
      requestConfig.body=dataStr;
    }

    try {
      const response = await fetch(url, requestConfig);
      const responseJson = await response.json();
      if(responseJson.resultCode == "00015"){
        router.push("login");
      }else{
        return responseJson;
      }
    } catch (error) {
      throw new Error(error);
    }
  } else {
    return new Promise((resolve, reject) => {
      let requestObj;
      if (window.XMLHttpRequest) {
        requestObj = new XMLHttpRequest();
      } else {
        requestObj = new ActiveXObject;
      }

      let sendData = '';
      if (type == 'POST') {
        sendData = JSON.stringify(data);
      }

      requestObj.open(type, url, true);
      requestObj.setRequestHeader("Content-type", "application/x-www-form-urlencoded");
      requestObj.send(sendData);

      requestObj.onreadystatechange = () => {
        if (requestObj.readyState == 4) {
          if (requestObj.status == 200) {
            let obj = requestObj.response
            if (typeof obj !== 'object') {
              obj = JSON.parse(obj);
            }
            resolve(obj)
          } else {
            reject(requestObj)
          }
        }
      }
    })
  }
}

export const parseObject = (data,dataStr) => {
    Object.keys(data).forEach(key => {
        if(Object.prototype.toString.call(data[key]).slice(8, -1)=='Object'){
            dataStr = parseObject(data[key],dataStr)
        }else{
            dataStr =dataStr + key + '=' + data[key] + '&';
        }
        
    })
    return dataStr;
}


