<!-- 状态栏 -->
<template>
    <div class="wraper">
    <!-- 提示信息 -->
        <!-- <div style="line-height: 50px;font-size: 18px;font-weight: 530;margin-left: 13px">{{this.$global.getTitle()}}</div> -->
        <div style="line-height: 50px;font-size: 18px;font-weight: 530;margin-left: 13px">云总机运营管理系统</div>
    </div>
</template>

<script>

export default {
    components: {
    },
    data() {
        return {
          checkText:'云总机配置管理---分机管理',
          callOutPhone: '', // 外呼电话
          CancelState: true,
          transferButton:true,
          onFocus: false,
          inFocus: false,
          second: 0,
          minute: 0,
          hour: 0,
          time: '00:00:00',
          status:true,
          disabled:false,

          //切换类名
          Dom:'',
          activeClass: 'bar_focus',
          mainClass: 'bar_btn',
        //来电接听状态
          mainInCallState:''

        }
    },
    created:function () {
        //   this.$Bar.getIvr(this);
    },
    
   

    methods: {
      /**
       * 签入
       */
      checkIn: function () {
        this.$Bar.checkLogin(this)
      },
      /**
       * 签出
       */
      checkOut: function () {
        this.$Bar.checkOut(this)
      },
      /**
       * 呼出
       */
      callOut: function () {
        this.$store.callStore.commit('setCallPhone', {callPhone: this.$data.callOutPhone});
//        this.$store.callStore.commit('setMainState', { MainState: 'busy' });
        this.$Bar.outCall(this)
//        this.$Bar.placeCall(this)
      },
      /**
       * 挂断
       */
      callOff: function () {
        this.$Bar._callOff(this);
        this.CancelState = true;
        this.$store.callStore.commit('setTransferState',{transferState: ''});
        this.$store.callStore.commit('setOutCall',{OutCall: {}});
      },
      /**
       * 来电转接
       */
      transfer: function () {
//        this.getTransfer();
        this.Dom =event.currentTarget;
      },
      hidetrans:function () {
        this.Dom.className = 'bar_btn'
      },
      showtrans:function () {
        this.Dom.className = 'bar_focus'
      },
      /**
       * 转IVR
       */
      callIvr: function () {
        this.getIvr()
      },
      /**
       * 取消咨询
       */
      CanCell: function () {
        this.$Bar.CancelCall(this);
        this.$store.callStore.commit('setTransferState', {transferState: 'ok'});
      },
      InCanCell:function () {
        let id = this.$store.callStore.getters.getTransUrl;
        this.$Bar.InCancelCall(this,id)
        this.$store.callStore.commit('setTransferState', {transferState: 'ok'});
      },
      /**
       * 转接拾回
       */
      reterieve: function () {
        this.getreterieve(this.$store.callStore.getters.getInCall.jobId, 1004)
      },
      /**
       * 置忙
       */
      busyIn: function () {
        this.$Bar.SetNoDisturb(this);

      },
      /**
       * 就绪
       */
      ready: function () {
        this.$Bar.SetReady(this);
      },
      /**
       * 事后处理
       */
      finish: function () {
        this.closeWork($.agentBar, this)
      },
      /**
       * 转满意度
       */
      Rate: function () {
        this.$Bar.transRate(this);
      },
      /**
       * 保持/拾回
       */
      keep:function () {
        this.$Bar.keepState(this);
      },
      shihui:function () {
        this.$Bar.StopKeep(this);
      },
//      开启计时
      timer: function () {
        let that = this;
        window.wtime = setInterval(function () {
          let str_sec = that.second;
          let str_min = that.minute;
          let str_hour = that.hour;
          if (that.second < 10) {
            str_sec = '0' + that.second
          }
          if (that.minute < 10) {
            str_min = "0" + that.minute;
          }

          if (that.hour < 10) {
            str_hour = "0" + that.hour;
          }
          that.time = str_hour + ':' + str_min + ':' + str_sec;
          that.second++;
          if (that.second > 59) {
            that.second = 0;
            that.minute++;
          }
          if (that.minute > 59) {
            that.second = 0;
            that.hour++;
          }
        }, 1000);
      },

      timer1: function () {
        let that = this;
        window.wtime = setInterval(function () {
          let str_sec = that.second;
          let str_min = that.minute;
          let str_hour = that.hour;
          if (that.second < 10) {
            str_sec = '0' + that.second
          }
          if (that.minute < 10) {
            str_min = "0" + that.minute;
          }

          if (that.hour < 10) {
            str_hour = "0" + that.hour;
          }
          that.time = str_hour + ':' + str_min + ':' + str_sec;
          that.second++;
          if (that.second > 59) {
            that.second = 0;
            that.minute++;
          }
          if (that.minute > 59) {
            that.second = 0;
            that.hour++;
          }
        }, 1000);
      },
      clearTimer: function () {
        clearInterval(window.wtime);
        this.second = 0;
        this.minute = 0;
        this.hour = 0;
        this.time = '00:00:00';
      },
      clearTimer1: function () {
        clearInterval(window.wtime);
        this.second = 0;
        this.minute = 0;
        this.hour = 0;
        this.time = '00:00:00';
      },
      show : function (e) {
        console.log(this.event)
      }
    },
  
  }
</script>

<style scoped lang="less">
  .fade-enter-active, .fade-leave-active {
    transition: opacity 0.5s
  }

  .fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */
  {
    opacity: 0
  }

  .Bar {
    margin-left: 220px;
    margin-top: 11px;
  }

  .v-tab {
    max-height: 280px;
    overflow: auto;
  }

  .wraper {
    width: 100%;
    height: 49px;
    position: relative;
    overflow: hidden;
     //z-index: 1; myz remove
    border-bottom: 1px solid #e6e6e6;

  .callout-phone {
    border: 1px solid #a0a0a0;
    padding: 2px;
    margin-left: 20px;
  }

  a {
    border: 1px solid gray;
    padding: 2px 8px;
  }

  .inner {
    width: 700px;
    overflow: hidden;

  }
  .i_wrapper{
    margin-left: 26px;
    height: 35px;
  }
  .i_wrapper div{
    float: left;
    width: 120px;
    height: 24px;
    line-height: 24px;
  }

  }
</style>



