<template>
<div id="login">
    <div class="wrapper" id="wrapper">
        <div id="login-box">
            <div id="login-po">
                <el-form :model="loginUser" labelPosition="left">
                    <el-form-item prop="username" label="" :label-width="formLabelWidth">
                        <el-input v-model.number="loginUser.username" :maxlength="32" placeholder="用户名" ref="inputRef" @keyup.enter.native="checkLogin"></el-input>
                    </el-form-item>
                    <el-form-item prop="password" label="" :label-width="formLabelWidth">
                        <el-input type="password" v-model.number="loginUser.password" :maxlength="32"  placeholder="密码" @keyup.enter.native="checkLogin"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="checkLogin()" style="width:312px;height:36px;margin-left:8px">确 定</el-button>
                </div>
            </div>
         </div>
    </div>
  </div>
</template>

<script>
import {
    checkUsernameAndPassword,
     getUserDetailInfo,
}from '@/api/getData';
import Vue from 'vue';
export default {
    data() {
        return {
            loginUser:{
                username: '',
                password: '',
            },
            formLabelWidth: '8px',
            isFocus: true,    //获得焦点
        };
    },
    methods: {
        toFirstPage(level) {
            if(level === '999'){
                this.$router.push({ path: "/enterpriseManage" });
            }else if(level === '800'){
                this.$router.push({ path: "/enterpriseAccount" });
            }else{
                this.$router.push({ path: "/extAccount" });
            }
            
        },
        async checkLogin(loginUser){
            try {
                let username = this.loginUser.username;
                var res = await checkUsernameAndPassword({
                    username: username,
                    password: this.loginUser.password,
                });
                if(res.resultCode == '0'){
                    window.name =  this.$moment(new Date()).format("YYYY-MM-DD HH:mm:ss");
                    this.$store.baseStore.commit('setUserInfo', res.result);
                    this.$store.baseStore.commit('setWindowname',  window.name);
                    if(res.result.managerLevel ==='999'){
                        this.$store.baseStore.dispatch('getAllDeptId');
                    }
                    this.toFirstPage(res.result.managerLevel);
                } else {
                    this.$message({
                        type: 'error',
                        message: res.result
                    });
                }
            } catch (error) {
                this.$message({
                    type: 'error',
                    message: error
                });
            }
        },
        async getUserDetailInfo(username){ //已经弃用
            try {   
                let query = {
                    username: username
                }
                //分页
                const res = await getUserDetailInfo(query);
                if (res.resultCode == '0') {
                     this.$store.baseStore.commit('setUserInfo', res.result);
                     this.toFirstPage();
                } else {
                    throw new Error(res.result)
                }
            } catch (err) {
                console.log('获取菜单失败', err);
            }
        },

    },
    mounted(){
        this.$refs.inputRef.$el.children[0].focus();
    }
    
};
Vue.directive('focus', function (el, option) {
    var defClass = 'el-input', defTag = 'input';
    var value = option.value || true;
    if (typeof value === 'boolean')
        value = { cls: defClass, tag: defTag, foc: value };
    else
        value = { cls: value.cls || defClass, tag: value.tag || defTag, foc: value.foc || false };
    if (el.classList.contains(value.cls) && value.foc)
        el.getElementsByTagName(value.tag)[0].focus();
});


</script>

<style lang="less" scoped>
body {
  margin: 0px;
  padding: 0px;
  background: #1f2d3d;
  font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB,
    Microsoft YaHei, SimSun, sans-serif;
  font-size: 14px;
  -webkit-font-smoothing: antialiased;
}
#login {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  background: url(../../assets/bg-1_h.jpg) no-repeat;
}
#login-box{
    width: 320px;
    height: 390px;
    padding: 0 30px;
    margin-left: -190px;
    margin-top: -175px;
    position: absolute;
    left: 50%;
    top: 45%;
    border-radius: 4px;
    background-color: #fff;
    background-color: rgba(255,255,255,.99);
    box-shadow: 0 5px 20px 0 rgba(0,0,0,.3);

}
#login-po{
   top: 25%;
    position: relative;

}
.el-row {  
    margin-bottom: 20px;  
    &:last-child {  
      margin-bottom: 0;  
    }  
}  
.el-button {
  padding-bottom: 20px;
}
.login-box {  
    margin-top:20%;  
    margin-left:40%;  
}  
.wrapper {
  min-width: 1280px;
  overflow: hidden;
  height: 100%;
}
.wrapper .inner {
  display: table;
  width: 100%;
  height: 100%;
}
.right-wraper {
  display: table-cell;
  height: 100%;
  position: relative;
}
.customer_alert {
  position: absolute;
  width: 408px;
  height: 36px;
  box-sizing: border-box;
  border-radius: 4px;
  text-align: center;
  line-height: 36px;
  font-size: 12px;
  z-index: 99999;
  top: 20px;
  left: 50%;
  margin-left: -204px;
}
.error_style {
  background: #ffd8d8;
  border: 1px solid #febebe;
}
.warning_style {
  background: #fff5ce;
  border: 1px solid #fff0b4;
}
.success_style {
  background: #e8fee7;
  border: 1px solid #d8f0d7;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>



